<template>
  <div class="tab-container">
    <div class="tab">
      <div class="item" v-for="(item,index) in weekList">{{item}}</div>
    </div>
    <div class="content">
      <slot></slot>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'left-tab',
    data() {
      return {
        weekList: ['周一', '周二', '周三', '周四', '周五', '周六', '周日'],
      };
    },
  };
</script>

<style lang="less" scoped>
  @import "../style/common";

.tab-container{
  display: flex;
  .wh(7.5,10);
}
.tab {
  width: 30%;
  height: 100%;
  .item {
   width: 100%;
    .height(1)
  }
}

</style>



