<template>
  <div>
    <ystHeader nav-title="宝宝食谱" show-back="true" show-more="true" @clickBack="clickBack" @clickMore="clickMore"></ystHeader>
    <div class="topTab">
      <div class="tab-item"  v-for="(item,index) in tabList"  @click="clickDate(index)">
        <img src=""/>
        <span>{{item}}</span>
      </div>
      <div class="border-line"></div>
      <div class="seleted-line-container">
        <div id="selected-line"></div>
      </div>
    </div>
    <div>
      <div class="food-time">
      <img src="">
      <span>早餐 07:00-07:30</span>
    </div>
      <div class="foods-container">
        <div class="food-item">
          <img src=""/>
          <span>太阳花煎鸡蛋</span>
        </div>
        <div class="food-item">
          <img src=""/>
          <span>太阳花煎鸡蛋</span>
        </div>
        <div class="food-item">
          <img src=""/>
          <span>太阳花煎鸡蛋</span>
        </div>
      </div>
    </div>
    <div class="yst-segmentation-line-view"></div>
    <div>
      <div class="food-time">
        <img src="">
        <span>午餐 12:00-12:30</span>
      </div>
      <div class="foods-container">
        <div class="food-item">
          <img src=""/>
          <span>太阳花煎鸡蛋 1ge</span>
        </div>
        <div class="food-item">
          <img src=""/>
          <span>太阳花煎鸡蛋 1ge</span>
        </div>
        <div class="food-item">
          <img src=""/>
          <span>太阳花煎鸡蛋 1ge</span>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
  import leftTable from '../../components/leftTab';
  import ystHeader from '../../components/common/ystHeader';
  import { navgationBack } from '../../utils/mRouter';

  export default {
    name: 'yst_baby-food',
    components: { leftTable, ystHeader },
    data() {
      return {
        tabList: ['周一', '周二', '周三', '周四', '周五', '周六', '周日'],
        foodsInfo: [{ title: '早餐 07:00-07:30', foods: [{ name: '太阳花煎鸡蛋', count: '1个', img: '' }] }, { title: '中餐 11:00-12:00', foods: [{ name: '太阳花煎鸡蛋', count: '1个', img: '' }] }],
        currentIndex: 1,
        DOM_line: null,
      };
    },
    methods: {
      clickBack() {
        console.log(`back${this.$route.name}`);
        navgationBack(-1, this);
      },
      clickMore() {
        console.log('click more');
      },
      clickDate(index) {
        this.currentIndex = index;
        const rate = 100 / this.tabList.length;
        this.DOM_line.style.left = `${index * rate}%`;
      },
    },
    mounted() {
      console.log(`mounted--------------------${this.name}`);
      this.DOM_line = document.getElementById('selected-line');
      const rate = 100 / this.tabList.length;
      this.DOM_line.style.width = `${rate}%`;
    },
    created() {
      console.log(`created--------------------${this.name}`);
    },
  };
</script>

<style lang="less" scoped>
  @import "../../style/common";
  .topTab {
    position: relative;
    .margin-top(0.88);
    .wh(7.02,0.8);
    .padding(0,0.24);
    .flx-x-between;
  }
  .tab-item {
    .flx-x-center;
    .fontSize-four;
    .text-color1;
    .height(0.76);
    img {
      .wh(0.24,0.24);
      background-color: goldenrod;
    }
    span{
      .margin-left(0.08);
    }
  }
  /*.tab-item-selected {
    border-bottom: #26a2ff solid 2px;
    .flx-x-center;
    .fontSize-four;
    .height(0.76);
    img {
      .wh(0.24,0.24);
      background-color: goldenrod;
    }
  }*/
  .seleted-line-container {
    position: absolute;
    bottom: 0;
    .width(7.02);
    height: 5px;
  }
  #selected-line {
    position: absolute;
    bottom: 0;
    left: 0%;
    height: 2px;
    background-color: #26a2ff;
    transition: left 0.5s;
  }
  .food-time {
    .wh(7.02,0.75);
    .padding(0,0.24);
    .flx-x-start;
    img {
      .wh(0.24,0.24);
    }
    span {
      .margin-left(0.16);
      .fontSize-four;
      .text-color1;
    }
  }
  .foods-container {
    .flx-wrap-between;
    .width(7.02);
    .margin(0,0.24);
  }
  .food-item {
    .flx-y-center;
    justify-content: flex-start;
    .wh(3.5,2.78);
    img {
      .wh(3.46,1.96);
      .borderRadius(0.08);
    }
    span {
      display: block;
      .fontSize-five;
      .text-color1;
      .margin-top(0.16);
    }
  }


</style>



