<template>
  <div class="navBar" >
    <div class="back-btn" @click="clickBack">
      <div  v-if="showBack">back</div>
      <slot></slot>
    </div>
    <div class="title" >{{navTitle}}</div>
    <div class="right-btn" >
      <div @click="clickMore" v-if="showMore">more</div>
      <slot></slot>
    </div>
    <div class="border-line"></div>
  </div>
</template>

<script>
  export default {
    name: 'yst-header',
    props: ['navTitle', 'showBack', 'showMore'],
    methods: {
      clickBack() {
        this.$emit('clickBack');
      },
      clickMore() {
        this.$emit('clickMore');
      },
    },
  };
</script>

<style lang="less" scoped>
  @import "../../style/common";
  .navBar {
    position: fixed;
    z-index: 999;
    top: 0;
    width: 100%;
    height: 44px;
    background-color: white;
    display: flex;
    justify-content: space-around;
    box-sizing: border-box;
  }
  .back-btn {
    width: 44px;
    height: 44px;
    background-color: #4caf50;
    font-size: 16px;
    padding-left: 10px;

  }
  .title {
    font-size: 18px;
    width: 200px;
    height: 44px;
    text-align: center;
    line-height: 44px;
  }
  .right-btn {
    width: 44px;
    height: 44px;
    background-color: #4caf50;
    font-size: 16px;
    padding-right: 10px;
  }
  .block-right-btn {
    width: 44px;
    height: 44px;
    padding-right: 10px;
  }
  .block-left-btn {
    width: 44px;
    height: 44px;
    padding-left: 10px;
  }

  .border-line {
    -webkit-transform:scale(0.5);
    transform:scale(0.5);
    position:absolute;
    border-bottom:1px solid @line-color;
    bottom: 0;
    left: -50%;
    right: -50%;
  }

</style>



